@charset "UTF-8";
/** common scss **/
/** object scss **/
/* ===== 基础样式 ===== */
body {
  line-height: 1.5;
  font-family: "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  color: #202121;
  background-color: #f4f7f6;
  text-align: center; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

button,
input,
select,
textarea {
  font-family: "Roboto", "Helvetica Neue", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif; }

input,
select,
textarea {
  font-size: 14px; }

textarea,
input {
  color: #000; }

textarea {
  resize: none;
  overflow-y: auto; }

.app {
  position: absolute;
  top: 0;
  bottom: 0;
  max-width: 100vw;
  display: flex;
  flex-direction: column; }
  .app header {
    flex: 1 1; }
  .app main {
    flex: 12 1; }

.layout-header-nav {
  padding: 8px 14px;
  display: flex;
  justify-content: center; }
  .layout-header-nav a {
    color: #202121; }

.nav-button {
  border-radius: 4px !important; }

.nav-box {
  box-sizing: border-box;
  width: calc(100vw - 28px);
  border-radius: 4px;
  padding: 12px;
  background-color: #ffffff; }
  .nav-box-text {
    font-size: 18px;
    margin: 0;
    font-weight: bold;
    line-height: 1.8; }
  .nav-box-subText {
    font-size: 12px;
    color: #bdc1c0; }

.nav-box.black .nav-box-text {
  color: #0e0e0e; }

.nav-box.red .nav-box-text {
  color: #dc0808; }

.nav-box.green .nav-box-text {
  color: #00da6d; }

.nav-box.blue .nav-box-text {
  color: #075cff; }

